/* eslint-disable no-continue */
/* eslint-disable no-unused-vars */
/* eslint-disable no-cond-assign */
/* eslint-disable no-useless-escape */
const ncname = '[a-zA-Z_][\\-\\.0-9_a-zA-Z]*'; // ÂåπÈÖçÊ†áÁ≠æÂêç
const qnameCapture = `((?:${ncname}\\:)?${ncname})`; // ÂåπÈÖçÁâπÊÆäÊ†áÁ≠æ
const startTagOpen = new RegExp(`^<${qnameCapture}`); // ÂåπÈÖçÊ†áÁ≠æÂºÄÂßã
const startTagClose = /^\s*(\/?)>/; // ÂåπÈÖçÊ†áÁ≠æÁªìÊùü  >
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // ÂåπÈÖçÊ†áÁ≠æÁªìÂ∞æ
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // ÂåπÈÖçÂ±ûÊÄß

// Ê†πËäÇÁÇπÔºåÂΩìÂâçÁà∂ËäÇÁÇπ
let root;
let currentParent;
//
const stack = [];
// ÂÖÉÁ¥†„ÄÅÊñáÊú¨ nodeType ÂÄº
const ELEMENT_TYPE = 1;
const TEXT_TYPE = 3;

// ÁîüÊàêast
function creatASTElement(tagName, attrs) {
  return {
    tag: tagName,
    type: ELEMENT_TYPE,
    children: [],
    attrs,
    parent: null,
  };
}

// Â§ÑÁêÜÂºÄÂßãÊ†áÁ≠æ
function handleStartTag({ tagName, attrs }) {
  const element = creatASTElement(tagName, attrs);

  if (!root) {
    root = element;
  }

  currentParent = element;
  stack.push(element);
}

// Â§ÑÁêÜÁªìÊùüÊ†áÁ≠æ
function handleEndTag(tagName) {
  // ÂèñÂá∫Ê†àÈ°∂ÂÖÉÁ¥†
  const element = stack.pop();

  currentParent = stack[stack.length - 1];

  if (currentParent) {
    element.parent = currentParent;
    currentParent.children.push(element);
  }
}

// Â§ÑÁêÜÊñáÊú¨
function handleChars(text) {
  // ÂéªÊéâÁ©∫Ê†º
  text = text.replace(/\s/g, '');
  if (text) {
    currentParent.children.push({
      type: TEXT_TYPE,
      text,
    });
  }
}

//  Â∞Ü HTML Â≠óÁ¨¶‰∏≤ËΩ¨Êç¢‰∏∫ AST„ÄÇ
export function parse(html) {
  while (html) {
    // Êü•Êâæ <
    const textEnd = html.indexOf('<');

    // Â¶ÇÊûú < Âú®Á¨¨‰∏Ä‰∏™ ËØÅÊòéÊé•‰∏ãÊù•Â∞±ÊòØ‰∏Ä‰∏™Ê†áÁ≠æ ‰∏çÁÆ°ÊòØÂºÄÂßãËøòÊòØÁªìÊùüÊ†áÁ≠æ
    if (textEnd === 0) {
      // ÂåπÈÖçÂºÄÂßãÊ†áÁ≠æ
      const startTagMatch = parseStartTag();
      if (startTagMatch) {
        // ÁîüÊàêAST
        handleStartTag(startTagMatch);
        continue;
      }

      // ÂåπÈÖçÁªìÊùüÊ†áÁ≠æ
      const endTagMatch = html.match(endTag);
      if (endTagMatch) {
        advance(endTagMatch[0].length);
        handleEndTag(endTagMatch[1]);
        continue;
      }
    }

    // ÊñáÊú¨
    let text;
    if (textEnd > -1) {
      text = html.substring(0, textEnd);
    }
    if (text) {
      advance(text.length);
      handleChars(text);
    }
  }

  // ÂåπÈÖçÂºÄÂßãÊ†áÁ≠æ
  function parseStartTag() {
    const start = html.match(startTagOpen);

    if (start) {
      const match = {
        tagName: start[1],
        attrs: [],
      };

      // ÂåπÈÖçÂà∞ÂêéÔºåÊà™ÂèñÊéâÂºÄÂßãÊ†áÁ≠æ
      advance(start[0].length);

      let end;
      let attr;

      while (
        !(end = html.match(startTagClose))
        && (attr = html.match(attribute))
      ) {
        advance(attr[0].length);
        attr = {
          name: attr[1],
          value: attr[3] || attr[4] || attr[5], // ËøôÈáåÊòØÂõ†‰∏∫Ê≠£ÂàôÊçïËé∑ÊîØÊåÅÂèåÂºïÂè∑ ÂçïÂºïÂè∑ ÂíåÊó†ÂºïÂè∑ÁöÑÂ±ûÊÄßÂÄº
        };
        match.attrs.push(attr);
      }

      if (end) {
        advance(1);
        return match;
      }
    }
  }

  function advance(n) {
    html = html.substring(n);
  }

  return root;
}
